<template>
  <van-nav-bar
    :title="title"
    :left-arrow="!leftArrow"
    @click-left="onClickLeft"
    :fixed="true"
    class="navbar"
    @click-right="onClickRight"
  >
    <img slot="right" :src="reload" alt>
  </van-nav-bar>
</template>
<script>
import { NavBar, Icon } from "vant";
import reload from "../../static/reload.svg";
export default {
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon
  },
  mounted() {
      if(this.$route.params.title){
        this.title=this.$route.params.title
      }else if(this.$route.meta && this.$route.meta.routename){
          this.title = this.$route.meta.routename[0].name;
      }
      this.leftArrow=((this.$route.name==="User")||(this.$route.name==="Products")||(this.$route.name==="LaborCosts"))
  },
  watch: {
    route(newV) {
      if(newV.params.title){
        this.title=newV.params.title
      }else if(newV.meta && newV.meta.routename){
          this.title = newV.meta.routename[0].name;
      }
    },
    routeName(newV){
      this.leftArrow=newV
    }
  },
  computed: {
    route() {
      return this.$route;
    },
    routeName(){
      if(this.$route.name){
        return (this.$route.name==="User")||(this.$route.name==="Products")||(this.$route.name==="LaborCosts")
      }
    }
  },
  data() {
    return {
      title: "",
      reload: reload,
      leftArrow:true
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      window.location = window.location.pathname+ '/'+new Date();
    }
  }
};
</script>
<style scoped lang="less">
.van-hairline--bottom::after {
  border: none;
}
.navbar {
  height: 1.2rem;
  box-shadow: 0px 0.1rem 0.2rem -0.1rem rgba(0, 0, 0, 0.8);
  border: none;

  img {
    height: 0.45rem;
    vertical-align: middle;
  }
}
</style>

